* {
  box-sizing: border-box;
}

body, h2, p {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: block;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+1), .stars__star:nth-child(-n+2), .stars__star:nth-child(-n+3), .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transition-duration: .3s;
  transform: scale(1.02);
}

.card:hover .card__title {
  color: #34568b;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: auto auto 40px;
  display: block;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__count {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__description {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
}

body {
  font-family: Roboto, sans-serif;
}
